<template>
    <div class="address-list">
        <div class="table-head">
            <el-button type="default" class="left" size="small" @click="delMsgFunc()" :disabled="tableData && tableData.hasOwnProperty('list') && tableData.list.length <= 0">{{ $L('选中删除') }}</el-button>
            <div class="right">
                <el-input :placeholder="$L('输入手机号')" size="small" v-model="inputValue" class="input-with-select"
                          clearable
                          @keyup.enter.native="$event.target.blur"
                          @change="searchFunc">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </div>
        </div>
        <div class="table">
            <div class="content-box" v-if="tableData && tableData.hasOwnProperty('list')">
                <el-table
                        size="small"
                        :empty-text="$L('暂无数据')"
                        :data="tableData.list"
                        :header-cell-style="{background:'#f0f1f5',padding:'10px 0',color:'#666'}" class="content-box"
                        @selection-change="handleSelectionChange">
                    <el-table-column
                            type="selection"
                            width="55">
                    </el-table-column>
                    <el-table-column
                            :label="$L('收货人')"
                            align="left"
                            min-width="80">
                        <div slot-scope="scope">
                            {{ scope.row.name }}
                        </div>
                    </el-table-column>
                    <el-table-column
                            :label="$L('手机号码')"
                            align="center"
                            width="120">
                        <div slot-scope="scope">
                            {{ scope.row.mobile }}
                        </div>
                    </el-table-column>
                    <el-table-column
                            :label="$L('所在地区')"
                            align="center"
                            min-width="120">
                        <div slot-scope="scope">
                            {{ scope.row.regionsName }}
                        </div>
                    </el-table-column>
                    <el-table-column
                            :label="$L('详细地址')"
                            align="left">
                        <div slot-scope="scope">
                            {{ scope.row.address }}
                        </div>
                    </el-table-column>
                    <el-table-column
                            :label="$L('邮政编码')"
                            align="left">
                        <div slot-scope="scope">
                            {{ scope.row.recCode }}
                        </div>
                    </el-table-column>
                    <el-table-column
                            :label="$L('默认地址')"
                            align="center">
                        <div slot-scope="scope">
                            <el-button class="mainFontColor mainBorderColor" v-if="scope.row.isDefault === 1">{{ $L('默认地址') }}</el-button>
                            <el-button type="text" v-else @click="setDefaultFunc(scope.row.id)">{{ $L('设为默认') }}</el-button>
                        </div>
                    </el-table-column>
                    <el-table-column
                            :label="$L('操作')"
                            align="center"
                            min-width="150">
                        <div slot-scope="scope" class="operate">
                            <el-button type="primary" @click="addAddressFunc(scope.row.id)">{{ $L('修改') }}</el-button>
                            <el-button type="default" @click="delMsgFunc(scope.row.id)">{{ $L('删除') }}</el-button>
                        </div>
                    </el-table-column>

                </el-table>
                <!-- 分页 -->
                <div class="list-pages" v-if="tableData.list !== null && tableData.allNum>0">
                    <el-pagination
                            background
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-size="pageSize"
                            layout="prev, pager, next, jumper"
                            :total="tableData.allNum">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import addAddress from "../../../components/home/addAddress.vue"
    export default {
        name: "addressList",
        components : {
            addAddress
        },
        data (){
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                addressNum:6,
                allAddressNum:20,
                inputValue:'',
                tableData: {},
                pageSize:10,
                currentPage:1,
                addressArr:[],
                addressId:0,

            }
        },
        inject: ['reload'],
        created () {
            this.getData();
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    p : _self.currentPage,
                    num : _self.pageSize,
                    keyword : _self.inputValue
                };
                _self.$api('getAddressList',arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(() => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(() => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(() => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },

            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            searchFunc(val){
                this.inputValue = val;
                this.getData();
            },
            handleSelectionChange(val){
                let arr = [];
                for (let i in val){
                    arr[i] = val[i].id;
                }
                this.addressArr = arr;
                console.log(this.addressArr);
            },
            delMsgFunc(id){
                const _self = this;
                if (!id && (Array.isArray(this.addressArr) && this.addressArr.length === 0)) {

                    //未知错误
                    _self.$confirm(_self.$L('您还没有选中地址信息，请勾选地址再做此操作'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('取消'),
                        type: 'warning',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(() => {

                    });
                    return ;
                }

                let addId;

                if (id) {
                    addId = id;
                } else {
                    addId = this.addressArr;
                }

                let arr = {
                    idRec : addId
                };
                _self.$confirm(_self.$L('此操作将永久删除该地址信息, 是否继续?'),_self.$L( '提示'), {
                    confirmButtonText:_self.$L( '确定'),
                    cancelButtonText:_self.$L( '取消'),
                    type: 'warning'
                }).then(() => {
                    _self.$api('deleteAddress', arr, function (reData) {
                        if (reData.flag === 1) {
                            _self.loading = false;
                            _self.$message({
                                type: 'success',
                                message: _self.$L('删除成功!')
                            });
                            _self.getData();
                            _self.reload();
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            console.log(reData);
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'home'});
                            });
                        }
                    }, function (error) {
                        console.log(error);
                        //网络请求错误
                        _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), { //todo:msg还未更换语言包语言包
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    });
                }).catch(() => {

                });
            },
            setDefaultFunc(id){
                const _self = this;
                _self.addressId = id;
                _self.loading = true;
                if (id !== null) {
                    _self.$api('setDefaultAddress',{idRec:_self.addressId} , function (reData) {
                        if (reData.flag === 1) {
                            _self.currentPage = 1;
                            _self.$message({
                                type: 'success',
                                message: _self.$L('设置默认地址成功!')
                            });
                            _self.getData();
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'home'});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    });
                }
            },
            addAddressFunc(id){
                const _self = this;
                _self.addressId = id;
                _self.$store.commit('setDialogObj', {
                    name: this.$L('添加收货地址'),
                    view: addAddress,
                    width: '600px',
                    data: {id:_self.addressId}
                });
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib";
     .address-list{
         .table-head{
             height: 32px;
             .left{
                 float: left;
             }
             .right{
                 float: right;
                 .font{
                     padding-left: 20px;
                     font-size: 16px;
                 }
                 .search{
                     width: 120px;
                 }
                 .input-with-select{
                     width: 100%;
                     padding-left: 10px;
                 }
             }
         }
         .table{
             padding: 20px;
             background: #fff;
             margin-top: 10px;

             .content-box{
                 background: #fff;
                 .content-box{
                     padding: 0;
                     margin-top: 10px;
                     .status{
                         .font-red{
                             color: #f00;
                         }
                         .font-grey{
                             color: #999;
                         }
                     }
                     .operate{
                         .el-button{
                             padding: 8px 28px;
                         }
                     }
                 }
                 .list-pages{
                     text-align: center;
                     padding: 40px 0 20px;
                 }
             }
             /deep/ .el-table__empty-block {
                 @extend .no-select;
                 position: relative;
                 width: 100%;
                 height: 380px;
                 line-height: 380px;
                 text-align: center;
                 background-color: #ffffff;
                 font-size: 16px;
                 color: #999;
                 margin-bottom: 20px;
             }
         }
         .error {
             position: absolute;
             z-index: 200;
             top: 0;
             left: 0;
             width: 100%;
             height: 100%;
             background: #FFFFFF;
             overflow: hidden;

             .error-tips {
                 position: absolute;
                 top: 50%;
                 left: 50%;
                 width: 320px;
                 transform: translate(-50%, -50%);
             }
         }
     }
</style>