<template>
    <div class="add-address" v-loading="loading">
        <el-form :model="addressForm"
                 :rules="addressRules"
                 size="mini"
                 ref="addressForm"
                 label-width="100px"
                 label-position="left"
                 class="address-form">
            <el-form-item :label="$L('智能地址识别')" v-if="$lang()=='lang_0'">
                <el-input v-model="addressMsg" size="mini" type="textarea"
                	placeholder="例如：宝小哥，13200000000，广东省珠海市香洲区某某街道某某大厦001号，519000"></el-input>
                <div class="el-form-item-tip">
            		<p class="el-form-item-tip-txt">{{$L('粘贴整段地址，请用逗号或空格分隔，')}}<br>{{$L('自动识别姓名、电话和地址')}}</p>
            		<p class="el-form-item-tip-btn">
	            		<el-button @click="getMsg" class="button">
	            			{{ $L('智能识别') }}
	            		</el-button>
            		</p>
                </div>	
            </el-form-item>
            <el-form-item :label="$L('收货人')" prop="name">
                <el-input v-model="addressForm.name" size="mini"></el-input>
            </el-form-item>
            <el-form-item :label="$L('手机号码')" prop="mobile">
                <el-input v-model="addressForm.mobile" size="mini"></el-input>
            </el-form-item>
            <el-form-item :label="$L('所在地区')" class="address" prop="regions">
                <region-view ref="regions" style="width: 310px" size="mini" v-model="addressForm.regions" :placeholder="defaultCity"></region-view>
            </el-form-item>
            <el-form-item :label="$L('详细地址')" prop="address">
                <el-input v-model="addressForm.address" size="mini"></el-input>
            </el-form-item>
            <el-form-item :label="$L('邮政编码')" prop="recCode">
                <el-input v-model="addressForm.recCode" size="mini"></el-input>
            </el-form-item>
            <el-form-item :label="$L('设为默认')">
                <el-switch v-model="addressForm.defaultAddress" size="mini" :width="switchWidth"></el-switch>
            </el-form-item>
            <el-form-item class="bottom-btns">
                <el-button type="default" @click="close" class="button">{{ $L('取消') }}</el-button>
                <el-button type="primary" @click="save" class="button">{{ $L('保存') }}</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import RegionView from "../common/regionView";
    export default {
        name: "addAddress",
        inject: ['reload'],
        components: {RegionView},
        data() {
            return {
                loading: true,
                switchWidth: 50,
                //如果是修改的，这里用于显示默认的城市信息
                defaultCity:'',
                addressForm: {
                    name: '',
                    mobile: '',
                    address: '',
                    defaultAddress: true,
                    regions: [],
                    recCode:''
                },
                addressRules: {
                    name: [
                        {required: true, message: this.$L('请输入收货人姓名')},
                    ],
                    mobile: [
                        {
                            required: true,
                            validator: (rule, value, callback) => {
                                value = parseInt(value);
                                if(isNaN(value)){
                                    callback(new Error(this.$L('请输入正确的手机号码或固定电话')));
                                }else{
                                    let patter = /^((1[3456789]\d{9})|(\d{6,16}))$/;
                                    if(!patter.test(value)){
                                        return callback(new Error(this.$L('手机号码或固定电话有误')));
                                    } else {
                                        callback();
                                    }

                                }
                            }
                        },
                    ],
                    address: [
                        {
                            required: true,
                            message:  this.$L('请输入详细地址')
                        },
                    ],
                    regions: [
                        {
                            required: true,
                            message:  this.$L('请选择所在地区'),
                            validator: (rule, value, callback) => {
                                if (typeof value !== 'object' || value.length < 1) {
                                    callback(new Error( this.$L('请选择所在地区')));
                                } else {
                                    callback();
                                }
                            }
                        },
                    ],
                },
                addressMsg:"",
            }
        },
        props: {
            data: {
                type: Object,
                default: () => {
                    return {};
                }
            }
        },
        created() {
            this.getData();
        },
        methods: {
        	getMsg(){
        		const _self = this;
        		if(this.addressMsg=="")
        			return;
    			_self.$getApi('getAddressByStr', {address:this.addressMsg}, function (reData) {
    				[_self.addressForm.name,
                    _self.addressForm.mobile,
                    _self.addressForm.address,
                    _self.addressForm.regions,
                    _self.addressForm.recCode,
    				_self.defaultCity] = [
    					reData.name,reData.mobile,reData.address,reData.regions,reData.recCode,reData.regionsName
    				];
            	},err=>{
            		console.info(_self.$L('识别有误，请检查填写格式是否正确。'));
            	});
        	},
            save() {
                this.$refs.addressForm.validate((valid) => {
                    if (valid) {
                        this.close();
                        const _self = this;
                        let arr = {
                            idRec : _self.data.id,
                            recName :  _self.addressForm.name,
                            regions :  _self.addressForm.regions,
                            recAddress :  _self.addressForm.address,
                            recMobile : _self.addressForm.mobile,
                            recCode : _self.addressForm.recCode,
                            isDefault :  _self.addressForm.defaultAddress == true ? 1 : 0,
                            lang:''
                        };
                        _self.$api('editAddress', arr, function (reData) {
                            if (reData.flag === 1) {
                                _self.reload();
                                _self.loading = false;
                            } else if (reData.flag === -1) {
                                //登录超时了
                                _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                    confirmButtonText: _self.$L('重新登录'),
                                    cancelButtonText: _self.$L('取消'),
                                    type: 'error',
                                    distinguishCancelAndClose: true
                                }).then(() => {
                                    _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                                }).catch(action => {
                                    _self.$gotoWebHome();
                                });
                            } else {
                                //未知错误
                                _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                    confirmButtonText: _self.$L('点击重试'),
                                    cancelButtonText: _self.$L('返回首页'),
                                    type: 'error',
                                    distinguishCancelAndClose: true
                                }).then(() => {
                                    _self.getData();
                                }).catch(action => {
                                    _self.$router.push({name: 'addressList'});
                                });
                            }
                        }, function (error) {
                            //网络请求错误
                            _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'addressList'});
                            });
                        });
                    } else {
                        return false;
                    }
                });
            },
            close() {
                this.$emit("closeDialog");
            },
            getData() {
                const _self = this;
                _self.loading = true;
                if (_self.data && _self.data.hasOwnProperty('id')) {
                    _self.$api('getAddressInfo', {idRec: _self.data.id}, function (reData) {
                        if (reData.flag === 1) {
                            _self.addressForm.name = reData.data.name;
                            _self.addressForm.mobile = reData.data.mobile;
                            _self.addressForm.defaultAddress = reData.data.isDefault === 1 ? true : false;
                            _self.addressForm.address = reData.data.address;
                            _self.addressForm.recCode = reData.data.recCode;
                            //用于默认显示
                            _self.defaultCity = reData.data.regionsName;
                            //默认的地址信息，让程序去数据库加字段存放
                            _self.addressForm.regions = [1,7,247,3021];
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'addressList'});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'addressList'});
                        });
                    });
                } else {
                    _self.loading = false;
                }
            },
        }
    }
</script>

<style lang="scss" scoped>
    .add-address {
        position: relative;
        padding: 20px;
        width: calc(100% - 150px);
        margin: 0 auto;
        .address-form {
            .el-form-item {
                margin-bottom: 18px;
                /deep/{
                	.el-form-item-tip{
                		display: flex;
                		padding-top: 4px;
	                	.el-form-item-tip-btn{
	                		margin-left: 4px;
	                	}
	                	.el-form-item-tip-txt{
	            		    color: rgb(230, 162, 60);
						    font-size: 12px;
						    line-height: 16px;
						    flex: 1;
	                	}
                	}
                	.el-form-item__error{
            		    line-height: 16px;
                	}
                }
            }
        }

        .bottom-btns {
            text-align: center;

            .button {
                padding: 10px 30px;
            }
        }
    }
</style>